.myCavnas {
  position: absolute;
  top: 0px;
  left: 0%;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.button {
  width: 75px;
  height: 75px;
  position: relative;
  top: 20px;
  left: 20px;
  margin-right: 30px;
  z-index: 3;
  float: left;
}

.txtarea {
  width: 100px;
  height: 30px;
  position: relative;
  top: 20px;
  right: 0px;
  margin-right: 0px;
  z-index: 3;
  float: right;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

.loading-wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 10;
  background: #0d0504;
  pointer-events: none;
  opacity: 1;
  -webkit-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
}

.loading-wrapper.hide {
  opacity: 0;
}

.loading {
  text-transform: uppercase;
  font-family: 'Cabin Condensed', sans-serif;
  font-weight: bold;
  font-size: 10vw;
  text-align: center;
  height: 120px;
  line-height: 110px;
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  bottom: 0;
  display: block;
  margin-top: -55px;
}

.wave {
  background-image: url(data:image/png;base64,);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  text-shadow: 0px 0px rgba(255, 255, 255, 0.06);
  -webkit-animation: wave-animation 1s infinite linear, loading-animation 6s infinite linear alternate;
  animation: wave-animation 1s infinite linear, loading-animation 6s infinite linear alternate;
  background-size: 200px 100px;
  background-repeat: repeat-x;
  opacity: 1;
}

@-webkit-keyframes wave-animation {
  0% {
    background-position: 0 bottom;
  }

  100% {
    background-position: 200px bottom;
  }
}

@keyframes wave-animation {
  0% {
    background-position: 0 bottom;
  }

  100% {
    background-position: 200px bottom;
  }
}

@-webkit-keyframes loading-animation {
  0% {
    background-size: 200px 0px;
  }

  100% {
    background-size: 200px 200px;
  }
}

@keyframes loading-animation {
  0% {
    background-size: 200px 0px;
  }

  100% {
    background-size: 200px 200px;
  }
}
